	module.exports = {"data":{"markdownRemark":{"html":"<p>Det første opus Grieg virkeliggjorde sitt livslange prosjekt i var i ”Humoresker” opus 6.</p>\n<p>I disse fire stykkene, komponert i 1865, lar han seg inspirere av og til å bruke motiver og vendinger fra den norske folkemusikken. Etter at Grieg møtte Rickard Nordraak i København sommeren 1864, fikk livet hans ny retning og mening. Han skulle vende seg mot det norske og personlige (den nasjonalromantiske idé) i sitt uttrykk. Dette manifesterte seg i Humoreskene. Stykkene overrasket både publikum og kritikere. Her sto det plutselig fram en komponist med en særegen originalitet og evne til å forene slektskapet til den norske folkemusikken med tidens nye, musikalske strømninger.</p>\n<p>Stykkene har nær tilknytning til norske folketoner – for eksempel i nr. 2, Tempo di Menuetto, har åpningsmotivet likhetstrekk med ”Alle mann hadde fota” og ”Grisen” (Lindemanns samling). Nr. 1 er en Springar og den tredje en Halling. Den 4. – Allegro alla burla – viser Griegs mesterskap innen harmonikk og konsentrasjon av det idérike stoffet.</p>\n<p>Våren 1866 var Grieg på vei fra sitt fire måneder lange opphold i Roma. På hjemveien stoppet han opp i Leipzig og stakk innom sitt gamle studiested. Videre i Berlin gikk han innom en musikkhandel og spurte helt anonymt etter sine «Humoresker», opus 6. Forhandleren kunne berette at disse var helt utsolgt, på grunn av den store interessen. Grieg ga seg ikke til kjenne, og kunne notere seg en god porsjon suksess.  </p>","timeToRead":1,"excerpt":"Det første opus Grieg virkeliggjorde sitt livslange prosjekt i var i ”Humoresker” opus 6. I disse fire stykkene, komponert i 1865, lar han…","id":"/Users/thomasheimstad/Sites/Obo/src/posts/opus6.md absPath of file >>> MarkdownRemark","frontmatter":{"workname":"Humoresker","composedin":"1865","startDate":"15.6","startTime":null,"location":"Troldhaugen","artists":["Joachim Carr"],"medialink":null,"title":"6","dedicatedTo":"Rikard Nordraak","author":"Erling Dahl Jr.","date":"2017-11-25T20:04:44.484Z","category":"opus","tags":["klaver"],"thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5047846889952152,"src":"/static/opus6-f196f8817e7d392531fa0dd9447b221b-1453d.jpg","srcSet":"/static/opus6-f196f8817e7d392531fa0dd9447b221b-a184f.jpg 480w,\n/static/opus6-f196f8817e7d392531fa0dd9447b221b-1453d.jpg 629w","sizes":"(max-width: 629px) 100vw, 629px"}}}},"fields":{"slug":"/opus6/","artist":[{"frontmatter":{"title":"Joachim Carr","category":"artister","tags":["pianist"],"thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1,"src":"/static/JoachimCarr-ebdd6f0bedc771c8355d2a10d67644b2-466fa.jpg","srcSet":"/static/JoachimCarr-ebdd6f0bedc771c8355d2a10d67644b2-27b23.jpg 90w,\n/static/JoachimCarr-ebdd6f0bedc771c8355d2a10d67644b2-0afbd.jpg 180w,\n/static/JoachimCarr-ebdd6f0bedc771c8355d2a10d67644b2-466fa.jpg 360w,\n/static/JoachimCarr-ebdd6f0bedc771c8355d2a10d67644b2-43088.jpg 540w,\n/static/JoachimCarr-ebdd6f0bedc771c8355d2a10d67644b2-545bd.jpg 720w,\n/static/JoachimCarr-ebdd6f0bedc771c8355d2a10d67644b2-8c1c4.jpg 1080w,\n/static/JoachimCarr-ebdd6f0bedc771c8355d2a10d67644b2-5c5cf.jpg 1280w","sizes":"(max-width: 360px) 100vw, 360px"}}}},"fields":{"slug":"/artister/joachim-carr/","opuses":[{"frontmatter":{"title":"21"}},{"frontmatter":{"title":"6"}}]}}],"opuses":null}}},"pathContext":{"slug":"/opus6/"}}

